.input {
  position: relative;
  padding: 16px 16px 16px 60px;
  width: 100%;
  font-size: 16px;
  line-height: 22px;
  color: #333333;
  border: 1px solid #cccccc;
  border-radius: 4px;
}
.input::placeholder {
  color: #999999;
}
.input--email {
  background-image: url("../img/email.svg");
  background-repeat: no-repeat;
  background-position: 16px center;
}
.input--password {
  background-image: url("../img/lock.svg");
  background-repeat: no-repeat;
  background-position: 16px center;
}
.input--checkbox {
  width: 18px;
  height: 18px;
}
.input--registration {
  padding-top: 16px;
  padding-bottom: 19px;
  padding-left: 52px;
  font-size: 14px;
  line-height: 19px;
}
@media (min-width: 768px) {
  .input--registration {
    padding: 16px 16px 16px 60px;
    font-size: 16px;
    line-height: 22px;
  }
}

.label {
  position: relative;
  font-size: 12px;
  line-height: 16px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .label {
    font-size: 14px;
    line-height: 19px;
  }
}
.label--checkbox {
  display: flex;
  align-items: center;
}
.label--email::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 16px;
  width: 20px;
  height: 16px;
  background-image: url("../img/email.svg");
  background-repeat: no-repeat;
  background-size: 20px 16px;
}
.label--checkbox::before {
  content: "";
  position: absolute;
  top: 0;
  left: -34px;
  bottom: 0;
  margin: auto;
  width: 18px;
  height: 18px;
  border: 2px solid #cccccc;
  border-radius: 4px;
}
.label--position::before {
  left: 16px;
}

.input-checkbox:checked + .label--checkbox::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: -31px;
  margin: auto;
  width: 12px;
  height: 9px;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
  background-size: 12px 9px;
  background-position: center;
}

.input-checkbox:checked + .label--position::after {
  left: 19px;
  background-image: url("../img/checkbox-green.svg");
}

